.case_wrapper {
    position: relative;
    .container {
        display: flex;
        @media screen and (max-width: 1024px) {
          padding-left: 0;
        }
        @media screen and (max-width: 600px) {
          display: flex;
          flex-direction: column;
        }
    }

    .container.case_container_start {
        justify-content: baseline;
    }

    .container.case_container_end {
        justify-content: end;

        @media screen and (max-width: 1024px) {
          justify-content: space-between;
          gap: 20px;
        }

    }
    margin-top: 110px;
    height: 615px;

    @media screen and (max-width: 1024px) {
      margin-top: 40px;
      height: 620px;
    }

    @media screen and (max-width: 768px) {
      margin-top: 26px;
      height: 580px;
    }

    @media screen and (max-width: 600px) {
      height: 100%;
      padding: 0 10px;
    }
}

.case_image {
    position: absolute;
    &.case_image-left {
        left: 0;
    }

    &.case_image-right {
        right: 0;
    }

    @media screen and (max-width: 1440px) {
      width: 500px;
    }

    @media screen and (max-width: 1024px) {
      position: initial;
    }

    @media screen and (max-width: 768px) {
      width: 400px;
    }
}

.case_desc {
    max-width: 470px;
    h1 {
        max-width: 310px;
        font-weight: 700;
        font-size: 36px;
        line-height: 48px;
        color: #4D533C;
    }
    p {
        font-weight: 400;
        font-size: 16px;
        line-height: 26px;
        color: rgba(33, 56, 66, 0.6);
        margin-top: 30px;
        padding-bottom: 90px;
    }

    @media screen and (max-width: 1440px) {
      h1 {
        font-size: 28px;
        line-height: 36px;
      }
      p {
        font-size: 14px;
        line-height: 24px;
        padding-bottom: 70px;
      }
    }

    @media screen and (max-width: 1024px) {
      h1 {
        font-size: 22px;
        line-height: 30px;
      }

      p {
        font-size: 12px;
        line-height: 22px;
        padding-bottom: 50px;
      }
    }

    @media screen and (max-width: 768px) {
      h1 {
        font-size: 18px;
        line-height: 24px;
      }

      p {
        padding-bottom: 0px;
        margin-top: 10px;
      }
    }
}

.link {
    font-size: 16px;
    font-size: 16px;
    line-height: 18px;
    text-decoration-line: underline;
    color: #D4D6D4;
}

.case-comment {
    max-width: 453px;
    position: absolute;
    z-index: 99;
    background-color: white;
    padding: 30px;
    box-shadow: 50px 50px 100px rgba(0, 0, 0, 0.145947);
    border-radius: 8px;

    &_left {
        left: 400px;
        top: 400px;

        @media screen and (max-width: 1440px){
          left: 280px;
          top: 320px;
        }

        @media screen and (max-width: 768px) {
          left: 252px;
          top: 268px;
        }

        @media screen and (max-width: 600px) {
          position: inherit;
          top: 0;
          left: 0;
        }
    }

    &_right {
        right: 400px;
        top: 400px;
    }

    p {
        margin-top: 0;
        font-style: italic;
        font-weight: 400;
        font-size: 16px;
        line-height: 26px;
        color: #4D533C;
    }

    .case-comment_title {
        display: flex;
        gap: 14px;
        h3 {
            font-weight: 700;
            font-size: 16px;
            line-height: 24px;
            color: #4D533C;
        }
        span {
            font-weight: 400;
            font-size: 14px;
            line-height: 21px;
            color: #152934;
        }
    }

    @media screen and (max-width: 768px) {
      padding: 20px;
      max-width: 300px;
      p {
        font-size: 14px;
        line-height: 22px;
      }
    }

    @media screen and (max-width: 600px) {
      margin: 0 10px;
      max-width: 100%;
      position: inherit;
      display: block;
      top: 10px;
      left: 0;
    }
}
