.ideas {
    margin-top: 140px;
    .container {
        & > h1 {
            max-width: 610px;
            font-weight: 700;
            font-size: 36px;
            line-height: 46px;
            color: #4D533C;
            @media screen and (max-width: 425px) {
              font-size: 24px;
              line-height: 38px;
            }
        }
    }

}

.ideas-list {
    list-style: none;
    margin-top: 20px;
    padding: 0;
    display: flex;
    display: flex;
    justify-content: space-between;

    @media screen and (max-width: 768px) {
      flex-basis: 100%;
      flex-wrap: wrap;
    }
}

.ideas_item {
    max-width: 275px;
    padding: 27px 42px;

    h2 {
        font-weight: 500;
        font-size: 24px;
        line-height: 34px;
        text-transform: uppercase;
        color: #4D533C;
    }

    p {
        font-weight: 400;
        font-size: 14px;
        line-height: 26px;
        color: rgba(33, 56, 66, 0.6);
    }

    @media screen and (max-width: 1024px) {
      padding: 18px 33px;
      h2 {
        font-size: 22px;
        line-height: 32px;
      }

      p {
        font-size: 12px;
        line-height: 22px;
      }
    }

    @media screen and (max-width: 768px) {
      flex-basis: 50%;
    }

    @media screen and (max-width: 505px) {
      padding: 14px 26px;
      h2 {
        font-size: 18px;
      }
    }

    @media screen and (max-width: 425px) {
      flex-basis: 100%;
    }
}
