.have_wrapper {
    margin-top: 312px;
    .container {
        display: flex;
        align-items: center;
        gap: 120px;
        h1 {
            max-width: 325px;
            font-weight: 700;
            font-size: 36px;
            line-height: 54px;
            color: #4D533C;
        }

        p {
            max-width: 518px;
            font-weight: 400;
            font-size: 16px;
            line-height: 26px;
            color: rgba(33, 56, 66, 0.6);
        }
    }

    @media screen and (max-width: 1024px) {
      margin-top: 200px;
      .container {
        gap: 40px;
        h1 {
          font-size: 26px;
          line-height: 48px;
        }

        p {
          max-width: 460px;
        }
      }
    }

    @media screen and (max-width: 768px) {
      margin-top: 100px;
      .container {
        flex-direction: column;
        align-items: baseline;
        gap: 0;
        h1 {
          max-width: 100%;
        }

        p {
          max-width: 100%;
          margin-top: 0;
        }
      }
    }

}
