footer {
    background-image: url('../img/footer_bg.png');
    padding: 310px 0px 30px 0px;
    margin-top: 320px;
    color: #B6C197;

    @media screen and (max-width: 1024px) {
      margin-top: 250px;
    }

    @media screen and (max-width: 768px) {
      padding: 240px 0px 20px 0px;
      margin-top: 170px;
    }

    @media screen and (max-width: 375px) {
      padding: 120px 0px 20px 0px;
      margin-top: 100px;
    }
}

.footer-title {
    color: white;
}

.footer-top {
    display: flex;
    gap: 128px;
    .footer-top_item {
        display: flex;
        flex-direction: column;
        gap: 12px;
    }

    @media screen and (max-width: 465px) {
      gap: 40px;
    }

    @media screen and (max-width: 375px) {
      flex-direction: column;
    }


}

.footer_item-list {
    display: flex;
    flex-direction: column;
    gap: 15px;
    list-style: none;
    padding: 0;
}

.footer-middle {
    display: flex;
    max-inline-size: 100%;
    justify-content: space-between;

    @media screen and (max-width: 768px) {
      flex-wrap: wrap;
      gap: 40px 0px;
    }

    .footer_item {
      flex-basis: 50%;
    }
}

.footer-bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 24px;
    @media screen and (max-width: 600px) {
      img {
        width: 130px;
      }
      span {
        font-size: 12px;
      }
    }

    @media screen and (max-width: 375px) {
      flex-direction: column;
      gap: 20px;
    }
}

.footer-desc {
    max-width: 252px;
    font-size: 16px;
    line-height: 26px;
}

.footer-address {
    font-weight: 700;
    font-size: 24px;
    line-height: 38px;
    color: #FFFFFF;
}

.social {
    display: flex;
    gap: 11px;
    list-style: none;
    padding: 0;
}

.social_link {
    display: block;
    width: 33px;
    height: 33px;
    background-color: #444A34;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    border-radius: 100%;
    background-size: 21px auto;
    fill: #5E6648;
}

.social_link_fc {
    background-image: url('../img/svg/facebook.svg');
}

.social_link_tw {
    background-image: url('../img/svg/twitter.svg');
}


.social_link_go {
    background-image: url('../img/svg/google.svg');
}
.social_link_lk {
    background-image: url('../img/svg/linkedin.svg');
}
